        //           }

        //           pdf.save('安全隐患通知单.pdf');
      })
    },
    addWatermark(doc, text) {
      A4[595.28, 841.89]
      const cav = document.createElement('canvas')
      cav.style.width = '200px'
      cav.style.height = '200px'
      const ctx = cav.getContext('2d')
      ctx.font = 'italic small-caps bold 16px Verdana'
      ctx.fillStyle = '#cfd8dc'
      ctx.rotate(-Math.PI / 6)
      ctx.fillText(text, 25, 180)
      const png = cav.toDataURL('image/png')
      const maxWidth = 150
      const maxHeight = 260
      let widthStart = 5
      let heightStart = 0
      do {
        doc.addImage(png, 'png', widthStart, heightStart)
        widthStart += 50
        if (widthStart > maxWidth) {
          widthStart = 5
          heightStart += 50
        }
      } while (heightStart < maxHeight)
    },
