import constant from '@/common/constant'
import http from '@/utils/http'


export function downloadpdf(params) {
  return http.get(`${constant.baseUrl}/v2/download`, params)
}

export function queryNoticeTime(params) {
  return http.postWithJson(`${constant.baseUrl}/v2/notice`, params)
}
